import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

//全局注册
Vue.use(Vuex);


//创建Store对象
const store = new Vuex.Store({
	strict:true,
	//状态数据
	state:{
		//主体分类
		topics:[
  			{name:'all',desc:'全部',selected:true},
  			{name:'good',desc:'精华',selected:false},
  			{name:'share',desc:'分享',selected:false},
  			{name:'ask',desc:'问答',selected:false},
  			{name:'job',desc:'招聘',selected:false},
  			{name:'dev',desc:'测试',selected:false}
  		],
  		//默认未登录
  		islogined:false,
  		//是否显示
  		isShowMoreMenus:false,
  		//未登录信息
		unloginmenus:{
			login:"登录",
			menu2:"注册"
		},
		//用户名
		loginname:'',
		//accesstoken
		accesstoken:'',
		//登录信息
		loginmenus:{
			create:'发表新话题',
			center:'个人中心',
			msg:'消息通知',
			logout:'退出'
		},
		//当前用户Id
		author_id:''
	},
	//状态数据的计算属性
	getters:{
		//计算显示的登录状态
		moreMenus(state){
			if(state.islogined){
				return state.loginmenus;
			}
			return state.unloginmenus
		},
		getTopicDescByTab:function(state){
			return function(tab){
				console.log(tab)
				for(var index = 0;index<state.topics.length;index++){
					if(state.topics[index].name == tab){
						return state.topics[index].desc
					}
				}
			}
		}
	},
	mutations,
	actions
});

//对外输出
export default store;



