
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
import axios from 'axios'
export default{
name:'create',
data:function(){
return {
title:'',
content:'',

toolbars: {
bold: true, // 粗体
italic: true,// 斜体
header: true,// 标题
underline: true,// 下划线

mark: true,// 标记


quote: true,// 引用
ol: true,// 有序列表
ul: true,// 无序列表
link: true,// 链接
imagelink: true,// 图片链接
code: true,// code
table: true,// 表格
subfield: true,// 是否需要分栏
fullscreen: true,// 全屏编辑
readmodel: true,// 沉浸式阅读
htmlcode: true,// 展示html源码
help: true
}
}
},
components:{
Group,XTextarea,
XButton,
mavonEditor,
Selector
},
methods:{
addtopic(){
var app = this;
this.$http.post('/topics',{
title:this.title,
content:this.content,
tab:'dev',
accesstoken:this.$store.state.accesstoken
}).then(function(response){
if(response.data.success&&response.data){


app.$router.replace('/topiclist/dev')
}
})
},

uploadImage(pos,file){
var app = this


console.log(pos);
console.log(file);

var params = new FormData();
params.append('smfile',file);


axios.post('https://sm.ms/api/upload',params,{

headers: { 'Content-Type': 'multipart/form-data' },
}).then(function(response){

app.$refs.md.$img2Url(pos, response.data.data.url);

console.log(response.data)
})

}
}
}



